body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

highlight {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
    padding: 0;
    border-width: 5;
    border-color: "#ffffff";
    background-color: "#000000"
}


.link {
  color: #007bff;
}
.link:hover {
   color: #FFF;
}

.sublink {
  color: black;
}
.sublink:hover {
  color: black;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-dash infinite 0.5s ease-in-out;
          animation: App-logo-dash infinite 0.5s ease-in-out;
  height: 20vmin;
  padding: 5vh;
  
}

.Line-break {
  width: 75vw;
  color: white;
  background-color: white;
  border-color: white;
}

.Button-bar {
align-items: center;
}
.Button {
  padding: 15px;
}

.App-header {
  padding: 1vh;
  background-color: #282c34;
  min-height: 100vh;
  min-width: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Title {
  max-width: 98vw;
  color: white;
}

.Pics {
  padding: 30px;
  border-radius: 10px;
  border-color: #748ab3;
  max-height: 50vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes App-logo-dash {
  0% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
  50% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  100%{
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}

@keyframes App-logo-dash {
  0% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
  50% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
  100%{
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
  }
}

